@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400&display=swap);
.App {
  font-family: sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  width: 100vw;
  background: rgb(236, 127, 249);
  font-family: "Roboto", sans-serif;
}
a {
  text-decoration: none;
  color: inherit;
}
.card-body a:hover {
  color: rgb(236, 127, 249);
  text-decoration: none;
}
.showCase {
  min-width: 80vw;
  min-height: 90vh;
  padding: 15px 25px;
  background: white;
  border-radius: 20px;
  box-shadow: 0.1rem 0.1rem 50px 0.01rem rgba(0, 0, 0, 0.4);
}

.navbar {
  background: none !important;
  padding-bottom: 5px;
}
.navbar a {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  color: black !important;
  border-bottom: 1px solid transparent;
}
.navbar li {
  font-size: 1.5vmax !important;
  padding-left: 10px;
}
.navbar-brand {
  font-size: 1.8vmax !important;
  /* padding-left: 10px; */
}
.navbar a:hover {
  border-bottom: 1px solid rgb(236, 127, 249);
  color: rgb(236, 127, 249) !important;
}
.Section {
  width: 80vw;
  /* height: 90vh; */
}
/* Home */
.Introduction {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 25px;
  flex-wrap: wrap;
  width: 100%;
  /* height: 90vh; */
  overflow: hidden;
}
.introText {
  margin-top: 2rem;
  max-width: 450px;
  /* max-height: 450px; */
}
.introText span {
  color: rgb(236, 127, 249);
}
.introButtons button {
  background: rgb(236, 127, 249);
  color: white;
  border: none;
  outline: none;
  border-radius: 20px;
  width: 200px;
  margin: auto;
  display: block;
  padding: 7px 9px;
  height: 40px;
  text-align: center;
}

.introButtons button:hover {
  color: rgb(236, 127, 249);
  background-color: white;
  border: 1px solid rgb(236, 127, 249);
}
.introButtons {
  /* margin-right: auto; */
  width: 100%;
}

.introImage img {
  -webkit-transform: scale(0.8);
          transform: scale(0.8);
  /* width: 80%;
  height: 80%; */
}

@media (max-width: 768px) {
  .introText h1 {
    font-size: 30px;
  }
}

/* About */
.introTitle {
  text-align: center;
  margin-bottom: 5px;
}
.ContactUs {
  padding: 10px 25px;
  width: 80vw;
}
/* ContactUs */
.contactForm {
  /* margin-top: 3rem; */
  width: 100%;
}
.ContactUsrow {
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.contactImage img {
  /* height: 500px; */
  -webkit-transform: scale(0.8);
          transform: scale(0.8);
  margin: 0;
}

.contactButtons button {
  background: rgb(236, 127, 249);
  color: white;
  border: none;
  outline: none;
  border-radius: 20px;
  width: 200px;
  margin: auto;
  display: block;
  padding: 7px 9px;
  height: 40px;
  text-align: center;
}

.contactButtons button:hover {
  color: rgb(236, 127, 249);
  background-color: white;
  border: 1px solid rgb(236, 127, 249);
}

.Services {
  width: 80vw;
  padding: 25px;
}

@media (max-width: 480px) {
  .introImage img {
  }
}

@media (max-width: 767px) {
  /* .introImage img {
    /* transform: scale(70%);
    */
  /* height: 300px;
    width: 400px;
  } */
}
@media (max-width: 1024px) {
  .introImage img {
    -webkit-transform: scale(0.7);
            transform: scale(0.7);
  }
  .contactImage img {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
  .Introduction {
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
  }
  .ContactUsrow {
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
  }
  .contactForm {
    width: 100%;
  }
  .navbar a {
    font-size: 18px !important;
  }
}
a {
  text-decoration: none !important;
  color: black !important;
}
.card img {
  size: fit-content;
}

/* Project  */

.projectcard {
  width: 300px;
}

.projectcard img {
  height: 200px !important;

  object-fit: cover;
}

